@import url("https://fonts.googleapis.com/css2?family=Indie+Flower&display=swap");

.ChefsPages {
  background-color: rgb(240, 240, 240);
  margin: 0;
}

.TitleChefsPage {
  text-align: center;
  font-size: 35px;
  font-family: "Indie Flower", cursive;
  color: green;
}

.ChefsBlock {
  display: flex;
  flex-wrap: wrap;
  flex-direction: "row";
  justify-content: center;
}

.ChefsCard {
  margin-left: 2%;
  border: 1px solid green;
  border-radius: 2%;
  margin-top: 2%;
  margin-bottom: 1%;
}
